import React from 'react'
import { Container, Image, Row, Col } from 'react-bootstrap'

import Layout from "../components/layout/layout"
import SEO from "../components/seo"

import teamHands from '../images/teamHands.jpg'

const AboutPage = () => (
<Layout>
    <SEO title="About" />
    <Container fluid="sm" className="text-center">
        <Row className="justify-content-md-center text-center">
            <Col md={8}>  
                Our goal is to engage people in the whole world to fight for their own health. 
                We believe it will be good for all of us and makes the world more beautyful.
            </Col>
        </Row>
        <Row>
            <Col>
                <p></p>
                <Image src={ teamHands } fluid/>
                <p></p>
            </Col>
        </Row>
        <Row>
            <Col>
                Will you join that challenge? 
            </Col>
        </Row>
    </Container>
</Layout>
)

export default AboutPage

